@charset "utf-8";

// form
input.datepicker {background-image: url("../../../../images/template11/resp/pink/icon/icon_datepicker.png");}

input[type="radio"] + label:before {
    background-image: url("../../../../images/template11/resp/pink/button/common_radio.png");
}
input[type="radio"]:checked + label:before,
input[type="radio"][disabled]:checked + label:before {
    background-image: url("../../../../images/template11/resp/pink/button/common_radio.png");
}

input[type="checkbox"] + label:before {
    background-image: url("../../../../images/template11/resp/pink/button/common_chk.png");
}
input[type="checkbox"]:checked + label:before,
input[type="checkbox"][disabled]:checked + label:before {
    background-image: url("../../../../images/template11/resp/pink/button/common_chk.png");
}

.checkList { 
    &.button {
        input[type="checkbox"]:checked + label {
            border-color: skin-color("pink", "borderColor2");
            background-color: skin-color("pink", "bgColor2");
        }
    }
}

.radioList {
    &.button {
        input[type="radio"] {
            & + label {
                border-radius: 2px;
            }

            &:checked + label {
                border-color: skin-color("pink", "borderColor2");
                background-color: skin-color("pink", "bgColor2");
            }

            &:focus + label:before {

                outline:1px solid skin-color("pink", "borderColor2");
            }
        }
    }
}



