@charset "utf-8";

// 나의 홈

.wgBox { 
  .txtBox {
    a {
      &.tit {
        &:hover {

          color:skin-color("pink","gradient")

        }
      }
    }
  };

  .buttonBox {
    > a {

      background: skin-color("pink", "btn");

      &:hover {
        background:skin-color("pink","btnHover1")
      }

    };

    > button {

      background: skin-color("pink", "btn");

      &:hover {
        background:skin-color("pink","btnHover1")
      }      
    }
  };

  .labelBox {
    > li {
      .label_blue {
        background-color:skin-color("pink","btn"); border:skin-color("pink","btn");            
      } 
    }
  };
};

.sub_contentBox {
    .pagination {
        .paging { 
            > li {
                > span {
                  .now_num{    
                    color:skin-color("pink","font2")
                  }
                }
              }
        }
    };

    .listBoxIn { 
      .txtBox {      
        a {
          &:hover {

            color:skin-color("pink","gradient")

          }
        }      
      }
    }
}


.ThumBox {
   .txtBox { 
     a.tit { 
       &:hover{
         color:skin-color('pink','font3')
       }
    }
  };

  .likeBox {

    .icon_heart{
      background:url("../../../../images/template11/resp/pink/icon/icon_heart.png") no-repeat;
    };

    span {

      color:skin-color('pink','font3') 

    }
  }
}

// 수강 후기 팝업 

.star_review {
   .review_txt {
    color:skin-color('pink','font3') 
  }
}

.join_write_process { 
  > ul {
    > li.active {
      .stepIn {
        
        background-color:skin-color('pink','font3') 

      }
    }

    > li:first-child.active {
      .step:after {

        border-color: skin-color("pink","gradient");

      }
    }

  }
}


// 수강신청 팝업

.join_write_comp {
  .circle {
    color:skin-color("pink","bgColor5");
  }
}

.join_write_comp p {
  color:skin-color("pink","font2");
}


