@charset "utf-8";

// 통합검색
.search-form {
    .search-input {
        .btn-search {
            background: skin-color("pink", "bgColor2");

            &:hover {
                background: skin-color("pink", "btnHover1");
            }
        }
    }

    .search-tag {
        .btn-search-tag {
            color: skin-color("pink", "font3");
        }
    }
}

// gnb
.gnb {
    .btn-gnb {
        .btn-gnb-color1 {
            color: skin-color("pink", "font1");

            &:after {
                background: skin-color("pink", "font1");
            }
        }
    }
}

.header-mobile {
    .header-mobile-tit:before {
        background: skin-color("pink", "gradient");
        background: linear-gradient(90deg, skin-color("pink", "gradientFrom-from") 0%, skin-color("pink", "gradientFrom-to") 100%);
    }

   .header-mobile-contact {
       .tel,
       .email {
           color: skin-color("pink", "font3");
       }
   }
}

// sub lnb
.lnb {
    background-color: skin-color("pink", "lnbBgColor");
}

// popup
.windowPopH {
    background: skin-color("pink", "gradient");
    background: linear-gradient(90deg, skin-color("pink", "gradientFrom-from") 0%, skin-color("pink", "gradientFrom-to") 100%);
}

.popup_wrap {
    .popupH {
        background: skin-color("pink", "gradient");
        background: linear-gradient(90deg, skin-color("pink", "gradientFrom-from") 0%, skin-color("pink", "gradientFrom-to") 100%);
    }
}


// text
.txt_point {
    color: skin-color("pink", "font3");
}

h4 .txt_point {
    color: skin-color("pink", "font1");
}

.txt_alert {
    color: skin-color("pink", "font1");

    &:before {
        background-image: url("../../../../images/template11/resp/pink/icon/icon_txt_alert.png");
    }
}

// 통합검색 결과
.search-global {
    .search-global-top {
        border-top-color: skin-color("pink", "borderColor1");
        background-color: skin-color("pink", "bgColor1");
    }

    .search-global-box {
        .btn-search-more {
            &:hover {
                background: skin-color("pink", "btnHover4");
            }
        }
    }

    .search-global-total {
        .search-global-word,
        .search-global-cnt {
            color: skin-color("pink", "font3");
        }
    }

   .search-global-board {
       .search-global-list {
           li:hover {
               .subject span {
                   color: skin-color("pink", "font1");
               }
           }
       }
   }
}

// 이용약관 동의
.terms-gate {
    .terms-gate-in .tit {
        color: skin-color("pink", "font1");
    }
}

// 사이트맵
.sitemap-wrap  {
    .sitemap-contents .tit {
        color: skin-color("pink", "font1");
    }
}