// color set
$comm-point1 : #e74c3c;
$comm-point2 : #ff833f;
$comm-point3 : #824bc8;
$comm-point4 : #00b6e3;
$comm-point5 : #4475db;
$comm-txtColor1 : #222;
$comm-txtColor2 : #666;
$comm-txtColor3 : #fff;
$comm-txtColor4 : #631bbf;
$comm-bg1 : #ebeff2;
$comm-bg2 : #f5f7f9;
$comm-bg3 : #759dd8;
$comm-bg4 : #a466c3;
$comm-border1 : #d7d7d7;
$comm-border2 : #a4a4a4;
$comm-border3 : #555;
$comm-border4 : #e7e7e7;

// green
$green-color1 : #2ab9c2;
$green-color2 : #118ba1;
$green-color3 : #168aac;
$green-color4 : #11a7b0;

// pink
$pink-color1 : #ed5f8a;
$pink-color2 : #d5306f;
$pink-color3 : #e43d88;
$pink-color4 : #e84274;

// pupple
$pupple-color1 : #612ba7;
$pupple-color2 : #40046a;
$pupple-color3 : #8343bc;
$pupple-color4 : #5335b9;

$skin-colors: (
    "green": (
        "btn" : $green-color1,
        "btnHover1": $green-color2,
        "btnHover2": #eefeff,
        "btnHover3": #1d91a6,
        "btnHover4": #f5feff,
        "gradientFrom-from": #15b7d3,
        "gradientFrom-to": $green-color3,
        "gradient" : $green-color3,
        "font1" : $green-color1,
        "font2" : $green-color2,
        "font3" : $green-color4,
        "font4" : $green-color3,
        "lnbBgColor" : rgba(42,185,194,.95),
        "bgColor1" : #f4fafa,
        "bgColor2" : $green-color1,
        "bgColor3" : $green-color2,
        "bgColor4" : #35bdc5,
        "bgColor5" : $green-color3,
        "bgColor6" : #f4f4f4,
        "borderColor1" : #d6e9e9,  // process_searchBox
        "borderColor2" : $green-color1
    ),
    "pink": (
        "btn" : $pink-color1,
        "btnHover1": $pink-color2,
        "btnHover2": #ffeff6,
        "btnHover3": #d73b76,
        "btnHover4": #fff8fb,
        "gradientFrom-from": #f66e97,
        "gradientFrom-to": $pink-color3,
        "gradient" : $pink-color3,
        "font1" : $pink-color1,
        "font2" : $pink-color2,
        "font3" : $pink-color4,
        "font4" : $pink-color3,
        "lnbBgColor" : rgba(237,95,138,.95),
        "bgColor1" : #fcf7f9,
        "bgColor2" : $pink-color1,
        "bgColor3" : $pink-color2,
        "bgColor4" : #ee6790,
        "bgColor5" : $pink-color3,
        "bgColor6" : #f4f4f4,
        "borderColor1" : #ecdae1, // process_searchBox
        "borderColor2" : $pink-color1
    ),
    "pupple": (
        "btn" : $pupple-color1,
        "btnHover": $pupple-color2,
        "gradientFrom-from": $pupple-color3,
        "gradientFrom-to": $pupple-color4,
        "gradient" : $pupple-color4,
        "font1" : $pupple-color3,
        "font2" : $pupple-color1,
        "font3" : #732eb0,
        "bgColor" : #faf7ff,
        "borderColor" : #e3dced
    )
);

@function skin-color($key: "null", $variant: "null") {
    $map: map-get($skin-colors, $key);
    @return map-get($map, $variant);
}