@charset "utf-8";

// lnb
.lnb-quick {
    background: skin-color("pink", "gradient");
    background: -moz-linear-gradient(left, skin-color("pink", "gradientFrom-from") 0%, skin-color("pink", "gradientFrom-to") 100%); background: -webkit-linear-gradient(left, skin-color("pink", "gradientFrom-from") 0%, skin-color("pink", "gradientFrom-to") 100%); background: linear-gradient(to right, skin-color("pink", "gradientFrom-from") 0%, skin-color("pink", "gradientFrom-to") 100%);
}

// 로그인 유저정보
.userinfo-main {
    .btn-tc-go {
        color: skin-color("pink", "font1");

        &:after {
            background-image: url("../../../../images/template11/resp/pink/icon/icon_gt_small.png");
        }
    }
}

.userinfo-course {
    .userinfo-course-info {
        .btn-classroom {
            background-color: skin-color("pink", "btn");

            &:hover {
                background-color: skin-color("pink", "btnHover1");
            }
        }
    }
}

// 인기과정
.course-main {
    .course-popularity {
        background: skin-color("pink", "bgColor6");

        &:before,
        &:after {
            background: skin-color("pink", "bgColor6");
            opacity: .75;
        }
    }
}