@charset "utf-8";

.lectureBoxL {
    .btn_white {
        &.active {
            border-color: skin-color("pink", "borderColor2");
            background: skin-color("pink", "bgColor2");

            &:hover {
                border-color: skin-color("pink", "borderColor2");
                background: skin-color("pink", "btnHover1");
            }
        }

        &:hover {
            background: skin-color("pink", "btnHover2");
        }
    }
}

.join_write_process {
    > ul {
        > li {
            &.active {
                .stepIn {
                    background-color: skin-color("pink", "bgColor5");
                }
            }

            &:first-child.active {
                .step:after {
                    border-left-color: skin-color("pink", "bgColor5");
                }
            }
        }
    }
}

.join_write_comp {
    .circle {
        background-color: skin-color("pink", "bgColor2");
    }

    p {
        color: skin-color("pink", "font1");
    }
}